import React, {Component } from 'react';

import Wrapper from '../components/Wrapper/Wrapper';

import Home from '../components/Home';
import Conseils from '../components/Conseils';
import Produits from '../components/Produits';
import Recettes from '../components/Recettes';
import User from '../components/Users';
import Parametres from '../components/Parametres/Parametres';
import Aide from '../components/Aide/Aide';
import About from '../components/About/About';
import Confidentialites from '../components/Confidentialites/Confidentialites';
import Contact from '../components/Contact/Contact';
import Sign from '../components/Login';
import Shop from '../components/Shop/Shop';
import Search from '../components/Search/SearchPage';
import Welcome from "../components/Welcome";
import ErrorPage from "../components/Error";

import {   Redirect, Route,  Switch
  } from "react-router-dom";

import SinglePost from 'components/Home/SinglePost';
import Coiffures from 'components/Coiffures';


class Routes extends Component{
   
  render(){
   
    
    return (
      
        <Route render={({location}) => (
          <>
         
              <div className={`App-container ${location.pathname.split("/")[1]}` }>

                <Switch location={location}>
                    {/* Welcome Page */}
                    <Route exact path="/"  component={Welcome} />  
                    {/* Sign page */ }                     
                    <Route exact path="/Sign" component={Sign }/>
                    
                   
                    {/* App Page */}
                    <Route>
                      <Switch>
                        <Wrapper>
                          { /* Home */}
                          <Route path="/Home"  exact component={Home} />

                          {/* Conseils */}
                          <Route>   
                            <Conseils/> 
                          </Route>
                          
                          { /* Recettes */}
                          <Route>
                            <Recettes/>
                          </Route>

                          {/* Produits */}
                          <Route  >
                            <Produits/>
                          </Route>

                      
                          {/* User */}
                          <Route >
                              <User/>
                          </Route>
                            
                          
                          <Route>
                            <Coiffures/>
                          </Route>


                          {/* Sidebar */}
                                                        
                          <Route exact path="/About" component={About}  />
                          <Route exact path="/Parametres" component={Parametres}/>  
                          <Route exact path="/Aide" component={Aide}/>
                          <Route exact path="/Contact" component={Contact} />
                          <Route exact path="/Confidentialites"  component={Confidentialites} />

                          {/* Toolbar */}
                          <Route exact path="/Shop" component={Shop} />
                          <Route exact path="/Search" component={Search} />
                          <Route path="/post/:id" component={SinglePost} />

                          {/**Social Média Links */}
                          <Route path='/Instagram' component={() => { 
                             window.open('https://www.instagram.com/awaa_arts/'); 
                              return null;
                          }}/>
                           <Route path='/Facebook' component={() => { 
                              window.open("https://www.facebook.com/lacasanieree", "_blank"); 
                              return null;
                          }}/>

                            <Route path='/Youtube' component={() => { 
                              window.open("https://www.youtube.com", "_blank"); 
                              return null;
                          }}/>

                          {/* Error Page */}
                          

                        </Wrapper>
                        
                      </Switch>
                    </Route>
                
                </Switch>
                </div>
          
                
              </>
            
        ) }/>
  
    );

  }
  
  
}


export default Routes;
