import React, {Component} from 'react';
import axios from 'axios';
import ShareLinks from 'components/Share/ShareLinks';
import BackgroundShare from 'components/Share/BackgroundShare';
import Loader from '../../../images/Double Ring-1s-200px.gif'

import  './SingleConseil.css';
import ContentSingleConseil from './ContentSingleConseil';
import clientConfig from "../../../client-config";

export default class SingleConseil extends React.Component {
    constructor( props ) {
		super( props );

		this.state = {
			loading : false,
			post: {},
			error: ''
		};
	}

	createMarkup = ( data ) => ({
		__html: data
	});

	componentDidMount() {
		const wordPressSiteUrl = clientConfig.siteUrl;

		this.setState( { loading: true }, () => {
            axios.get(`${wordPressSiteUrl}/wp-json/wp/v2/conseil/${this.props.match.params.id}?_embed`)
            .then( res => {

                if ( Object.keys( res.data ).length ) {
                    this.setState( { loading: false, post: res.data } );
                } else {
                    this.setState( { loading: false, error: 'No Posts Found' } );
                }
            } )
            .catch( err => this.setState( { loading: false, error: err.response.data.message } ) );
    } )
}

    handleShare = () => {
        this.setState((prevState) => {
          return {showShare: !prevState.showShare};
        });
    };
    backdropClickHandler = () => {
        this.setState({showShare: false});
      }
    render() {
       
        const { post, loading, error } = this.state;
        const wPsiteUrl = clientConfig.siteUrl;
        let backdrop;
        if (this.state.showShare){
            backdrop =<BackgroundShare click={this.backdropClickHandler} />;
        }
        return (
            
            <div className="single-page-conseil  page" style={{minHeight: "100%"}}>
                 
                 { error && <div className="alert-error" dangerouslySetInnerHTML={ this.createMarkup( error ) }/> }
                { Object.keys( post).length ?  (
                    <>
                        <div className="wrap-article single-conseils" style={{minHeight: "100%"}}> 
                         
                            <ContentSingleConseil 
                            conseil={post} 
                            src={post._embedded['wp:featuredmedia'][0].source_url} 
                            alt={post._embedded['wp:featuredmedia'][0].slug} 
                            background={{background: `url(${post._embedded['wp:featuredmedia'][0].source_url})`}}
                            handleClickShare={this.handleShare}
                            />
                        </div>        
                        <ShareLinks 
                        title="cette article"  
                        size={36} 
                        show={this.state.showShare} 
                        url={`${wPsiteUrl}/conseil/${post.id}`} 
                        value={post.link}/> 
                    </>                  
                    
                ) : ''}
                {loading && <img className="loader" src={Loader} alt="loader"/>}
                
                {backdrop}
            </div>
        
        
        )
    }
}


