import React from 'react';

import Conseils from './ConseilsRoute';

import  './Conseils.css';

import ScrollToTop from '../../AppRoute/ScrollToTop';

import {     Switch, Route, withRouter
} from "react-router-dom";



import SinglePost from './ArticlesConseils/SingleConseil';
import Share from 'components/Share';





class ConseilsPage extends React.Component {
      
  
    render() {
    
          return (
            
               
                 <ScrollToTop>
                  <Route render={({location}) => (

                    <div className="conseils page">
                          <Switch location={location}>

                                <Route exact path="/Conseils" component={Conseils} />                              
                                <Route path="/conseil/:id" component={SinglePost} />
                               
                          </Switch>
                        </div>
                  ) }/>
                  </ScrollToTop>
            
           
         
          );
        }
      }
      
      export default withRouter(ConseilsPage );
