import React, {useEffect, useState} from 'react'
import SearchBar from 'components/Search/SearchBar';



const HeaderCoiffures = (props) => {

    const [stickyBar, setStickyBar] = useState(false);

    
    useEffect(_ => {
        const changeBackground = _ => { 
          if (window.pageYOffset > 10) {
            setStickyBar(true)
          } else {
            setStickyBar(false)
          }
        }
        window.addEventListener('scroll', changeBackground)
        return _ => {
          window.removeEventListener('scroll', changeBackground)
        }
      }, [])

    return (
        <header  className={stickyBar ? 'style-header sticky': 'style-header'}>
            
            <div className="style-bg">                
                <div className='style-header-content' >                
                    <SearchBar/>
                    <div className="by-category clearfix">
                        <div className="filter-box-wrap">
                            <button className="filterbtn all-types">Tout</button>
                            <button className="filterbtn">Tresses</button>
                            <button className="filterbtn">Wigs</button>
                            <button className="filterbtn">Twists</button>
                        </div>
                    </div>             
                </div>
            </div>
        </header>  
    )
}

export default HeaderCoiffures
