import React from 'react';
import {Link} from 'react-router-dom';
import  './Search.css';
import BackLink from '../Back/BackLink';
import { FaSearch } from "react-icons/fa";

class SearchBar extends React.Component {
  state = {searchValue: '',};
  handleOnChange = event => {this.setState({ searchValue: event.target.value });};

  makeApiCall = searchInput => {var searchUrl = `http://localhost/wordpress/api/json/v1/1/search.php?s=${searchInput}`;
    fetch(searchUrl)
    .then(response => {return response.json();})
    .then(jsonData => {console.log(jsonData.meals);});};
  
  render() {

    
    return (
        <>
          <div className="search-container">
            
            <div className="navbar-toggle">              
              <BackLink/>
            </div>

            <input 
              className="search"
              name="text"
              type="text"
              placeholder="Search"
              onChange={event => this.handleOnChange(event)}
              value={this.state.searchValue}
            />
       
            <button className="submitSearch" type="submit">
              <FaSearch/>
            </button>

          </div>
        </>
    );
  }
  };


  

  
 export default SearchBar;