import React from 'react'
import renderHTML from 'react-render-html';
import Moment from 'react-moment';
 

import BackLink from '../../Back/BackLink';
import Marker from '../../Marker';
import NavBg from '../../Menu/Toolbar/NavBg';
import ShareBtn from '../../Share/ShareBtn';
import HalfRating from '../../Rating';
import { Link } from 'react-router-dom';


const ContentSingleRecette = ({post, alt, src, background, handleClickShare}) => {
    return (
        <article>

            <nav>
                <NavBg ><h1>{post.title.rendered}</h1> </NavBg> 
                <BackLink/>
            </nav>
            
            <div className="post-image" style={background}>
                <img className="center" src={src} alt={alt}  />
                
            </div>
            <div className="post-content">
                <Marker/>
                <div className="post-title"> <h1>{post.title.rendered}</h1> </div>    
                <div className="post-meta clearfix" >
                    <div className="post-date" ><Moment format=" DD MMM " locale='fr'>{post.date}</Moment></div>
                    <div className="cat">{post.genre[0].slug}</div>
                </div>                            
                <div className="content">
                    {renderHTML ( post.content.rendered )}
                </div>                                                          
            </div>        

            <div className="post-footer">
                <div className="share">
                    <span  style={ styles.text}>Partagez cette article</span>  
                    <ShareBtn  style={ styles.sharebtn} handleClick={handleClickShare} />
                </div>
                
                 <div className="rate" >
                    Les avis sur cette article : <Link></Link> Avis <HalfRating /> 
                </div>  
            </div> 
            
        </article>
    )
}
const styles = {    
    sharebtn: {
        marginBottom: '10px',
        color: 'rgb(199 78 118)',
    },
    text: {
        fontWeight: 'bold',
        color: 'rgb(56 56 56)',
    }
}
export default ContentSingleRecette
