import ShareBtn from 'components/Share/ShareBtn';
import React from 'react';
import Moment from 'react-moment';

import Marker from '../Marker';

const ContentConseil = ({children, background, jour, mois, alt, onClick, handleClickShare}) => {
    
    
    return (
        
        <article className="post-box">
            <div className="post-entry">
                <div href="" className="post-link">
                    <div className="post-thumbnail" >
                        <img src={background} style={ styles.card} alt={alt}/>
                        <div className="post-cover"></div>
                    </div>
                </div>                                            
                <h2>{ children}</h2>
                
                <div className="metaData">
                    <div className="date">
                        <p className="publi-jour">
                            <Moment format="DD">{jour}</Moment> 
                        </p>

                        <p className="publi-mois"> 
                            <Moment format="MMM" locale="fr">{mois}</Moment>
                        </p>
                    </div>
                </div>
                <ShareBtn  style={ styles.sharebtn} handleClick={handleClickShare} />
                <Marker onClick={onClick} />
            </div>                    
        </article>  
      
        
           
    )
}
const styles = {
    card: {
        objectFit: 'cover',
        width:' 100%',
        height: 'inherit',
        position: 'relative',
        borderRadius: '5% 95% 5% 95% / 77% 21% 79% 23%'
    },
    sharebtn: {
        position: 'absolute',
        bottom: '20px',
        right: '10px',
        color: '#fff'
    }
}
export default ContentConseil;
