import React, { useContext, useState } from 'react';
import { Redirect } from "react-router-dom";
import Loader from '../layouts/Loader'
import axios from 'axios';
import clientConfig from '../../client-config';
import AppContext from "../context/AppContext";


import { ReactComponent as ProfileIcon }   from '../../images/icons/at-solid.svg';
import { ReactComponent as CadenasIcon }   from '../../images/icons/cadenas.svg';
import InputField from './SignComponents/InputField';
import SubmitButton from './SignComponents/SubmitButton';
import { ReactComponent as LoginIcon}   from '../../images/icons/login.svg';
import PassOublie from './SignComponents/PassOublie';

const SigninForm = () => {

    const [ store, setStore ] = useContext( AppContext );

    const [ loginFields, setLoginFields ] = useState({
        username: '',
        password: '',
        userNiceName: '',
        userEmail: '',
        loading: false,
        error: ''
    });

    const createMarkup = ( data ) => ({
        __html: data
    });

    const onFormSubmit = ( event ) => {
        event.preventDefault();

        const siteUrl = clientConfig.siteUrl;

        const loginData = {
            username: loginFields.username,
            password: loginFields.password,
        };

        setLoginFields( { ...loginFields, loading: true } );

        axios.post( `${siteUrl}/wp-json/jwt-auth/v1/token`, loginData )
        .then( res => {

            if ( undefined === res.data.token ) {
                setLoginFields( {
                    ...loginFields,
                    error: res.data.message,
                    loading: false }
                    );
                return;
            }

            const { token, user_nicename, user_email } = res.data;

            localStorage.setItem( 'token', token );
            localStorage.setItem( 'userName', user_nicename );

            setStore({
                ...store,
                userName: user_nicename,
                token: token
            });

            setLoginFields( {
                ...loginFields,
                loading: false,
                token: token,
                userNiceName: user_nicename,
                userEmail: user_email,
            } )
        } )
        .catch( err => {
            setLoginFields( { ...loginFields, error: err.response.data.message, loading: false } );
        } )
    };

    const handleOnChange = ( event ) => {
        setLoginFields( { ...loginFields, [event.target.name]: event.target.value } );
    };


    const { username, password, userNiceName, error, loading } = loginFields;

    if ( store.token ) {
        return ( <Redirect to={"/Home"} noThrow /> )
    } else {

        return(
            <React.Fragment>
                <div className="base-container">
                   
                    <form className="sign-formgroup" onSubmit={ onFormSubmit } >
                        <div className="form-title"><h1>Connectez vous et mettez à jour votre routine !</h1></div>
                        <div className="sign-box ">
                            <div className="form-group">
                                <ProfileIcon/>
                                <InputField 
                                type="username" 
                                placeholder="username"
                                type="text"
                                name="username"
                                value={ username }
                                onChange={ handleOnChange }
                                    
                                />
                            </div>                            
                    
                            <div className="form-group">
                                <CadenasIcon/>
                                <InputField 
                                type="password" 
                                className="form-control" 
                                id="password" 
                                placeholder="Mot de passe"
                                name="password"
                                value={ password }
                                onChange={ handleOnChange }
                                />

                                <div className="hidePW">
                                    <i className="fas fa-eye-slash"></i>
                                </div>
                            </div>
                        </div>
                        <div className="sign-footer">
                            <div className="submitButton">
                                <SubmitButton 
                                    className="submit"
                                    type="submit"
                                    text={loading ? "authentification..." : "valider" }
                                    
                                /*disabled='' this.state.buttonDisabled onClick={ () => this.doLogin() */
                                    > 
                                    <LoginIcon/>  
                                </SubmitButton>  
                            </div>      
                            
                        </div>
                        
                    </form>  <PassOublie/>    
                    { error && <div className="alert alert-danger" dangerouslySetInnerHTML={ createMarkup( error ) }/> }              
                </div>
            </React.Fragment>
        )
    }
  
}
export default SigninForm

