import React, { Component } from 'react'
import { Link } from 'react-router-dom'

const ImageCard = (props)=> {
  
  
    

     
  
      return (
        <Link to={props.link}>
            <img ref={props.ref} src={props.src} alt={props.alt} />
        </Link>
          
        
      );
    
  }

export default ImageCard
