import React, {Component } from 'react';
import axios from 'axios';
import Main from '../Wrapper/Main'
import Header from '../Header';
import clientConfig from "../../client-config";
import CatRecettes from './CatRecettes';

import Loader from '../layouts/Loader';
import 'swiper/swiper-bundle.css';

import ShareLinks from 'components/Share/ShareLinks';
import Backdrop from 'components/Menu/Backdrop';

import RecetteList from './RecettesList';



class Recettes extends Component {
    constructor( props ) {
		super( props );

		this.state = {
			loading : false,
			categories: [],
			error: ''
		};
	}

	createMarkup = ( data ) => ({
		__html: data
	});
 
    componentDidMount() {
        const siteUrl = clientConfig.siteUrl;
        this.setState( { loading: true }, () => {
            axios.get(`${siteUrl}/wp-json/custom/v1/recettes_categories`)
            .then( res => {
                if ( 200 === res.status ) {
                    if ( res.data.length ) {
                        this.setState( { loading: false, categories: res.data } );
                    } else {
                        this.setState( { loading: false, error: 'Aucun article trouvé' } );
                    }
                }

            } )
            .catch( err => this.setState( { loading: false, error: err } ) );
        } )       
    }

    handleShare = () => {
        this.setState((prevState) => {
          return {showShare: !prevState.showShare};
        });
    };
    backdropClickHandler = () => {
        this.setState({showShare: false});
    }
    
    render() {
        let backdrop;
            if (this.state.showShare){
            backdrop =<Backdrop click={this.backdropClickHandler} />;
        }
        const {categories, loading, error, showShare} = this.state;  
        return (
            <div className="page recettes">
                {/**backdrop*/}
                <Header title="Recettes"><p>In nimia in agitationibus summum species sudant solito longiores.</p></Header>
                <Main> 
                    
                    <div className="page-recettes">                        
                        { error && <div className="alert-error" dangerouslySetInnerHTML={ this.createMarkup( error ) }/> }
                        {categories.length > 0 ?  (
                            <div className="recettes-content article-container clearfix">
                                {categories.map((category, index) => {
                                    const genre = category.slug;
                                    return(
                                        <CatRecettes key={index} classname={genre} title={category.title}>
                                            <RecetteList genre={genre}
                                                show={showShare} 
                                                
                                                showShare={showShare} />    
                                        </CatRecettes>
                                    )
                                })}                                
                            </div>
                        ) : ''}{loading && <Loader />}                        
                    </div>
                </Main>               
            </div>
        )
    }
}

 
export default Recettes;
