import React from 'react';
import './Footer.css'
import FooterNav from './FooterNav'
import { getUserName } from "../functions";
import { FaMagic, FaMortarPestle, FaThList, FaUser, FaImage} from 'react-icons/fa'
import { ReactComponent as HomeBtn }   from '../../images/icons/home.svg';

const Footer = () => {

    const userName = ( getUserName() ) ? getUserName() : '';

    const onglets = [
        
        {title: "Conseils", link: "/Conseils", icon: <FaThList/> },
        {title: "Produits", link: "/Produits", icon:  <FaMagic/>},
        {title: "Home", link: "/Home", icon: <HomeBtn/> },
        {title: "Recettes", link: "/Recettes", icon: <FaMortarPestle/> },
        {title: "User", link: "/User", icon: <FaUser/> },
    ]
    return (
    <footer>
        <div>
            <nav>
                <ul className="navbar">   
                    { onglets.map((onglet, index) =>(
                        <FooterNav key={index} title={onglet.title} link={onglet.link} icon={onglet.icon} />
                    ))}
                </ul>
            </nav>
        </div>        
    </footer>       
    )
}
export default Footer;
