import React, {useState, useRef, useEffect, useCallback} from 'react'
import { BiEdit } from "react-icons/bi";
import styled from 'styled-components';
import { MdClose } from 'react-icons/md';
import InputModal from './InputModal';
import FormDialogComponent from './FormDialog';
import FormUpdateComponent from './FormUpdate';

const Background = styled.div`
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
`;

function TodoForm(props) {
    const [inputValue, setInputValue] = useState(props.edit ? props.edit.value : '');

    
    const inputRef = useRef(null);
/*
    useEffect(() => {
        inputRef.current.focus();
    })*/

    const handleChange = (e) => {
        setInputValue(e.target.value);
    }

    const handleSubmit = (e) =>{
        e.preventDefault();

        props.onSubmit({
            id: Math.floor(Math.random() * 10000 ),
            text: inputValue
            }
        );
        setInputValue('');
    }
    

    return (
        <form className="routine-form" handleSubmit={handleSubmit}>
            {props.edit ? 
               
                <div className="update" >
                        <input 
                            type="text" 
                            className="todo-input edit" 
                            placeholder="Modifier l'étape"
                            name="text"
                            value={inputValue} 
                            onChange={handleChange} 
                            
                            ref={inputRef}
                            />

                        <button className="routineSubBtn todo-button edit" onClick={handleSubmit} type="submit"> 
                        <i className="fas fa-pencil-alt"></i> modifier
                            
                        </button> 
                    </div>
                    : 
                <>
                    <FormDialogComponent
                    open ={props.isDialogOpen}
                    handleClose={props.handleDialogClose}                    
                    value={inputValue} 
                    onChange={handleChange} 
                    handleSubmit={handleSubmit}
                    ref={inputRef} />
                   
                    {/** <InputModal
                   value={inputValue} 
                   onChange={handleChange} 
                   ref={inputRef}
                   showModal={props.showModal}
                   setShowModal={props.setShowModal}
                   /> */}                  
                   
                   
                </> 
            }
            

        </form>
    )
}

export default TodoForm
