import React, { Fragment } from 'react';

import { BrowserRouter as Router} from "react-router-dom";
import ScrollToTop from './AppRoute/ScrollToTop'

import Routes from './AppRoute/Routes';

import AppProvider from "./components/context/AppProvider";



import './App.css';

function App() {
  return (
    <div className="App">
      <AppProvider>
        <Router >
          <Fragment>
            <ScrollToTop>
              <Routes />
            </ScrollToTop>
          </Fragment>
        </Router>
      </AppProvider>
      
    </div>
  );
}

export default App;