import React, { useRef, useEffect, useCallback } from 'react';
import {Button, DialogContent, Dialog, DialogTitle,  DialogActions } from '@material-ui/core'

const FormDialogComponent = (props) => {

    return (
        <Dialog open={props.open}  style={{minWidth: "60vw"}} > 
            <DialogTitle>
                Add Todo
            </DialogTitle>
            <DialogContent>
                <input 
                 type="text" 
                 className="todo-input" 
                 placeholder="Ajouter une étape"
                 value={props.value} 
                 name="text"
                 onChange={props.onChange} 
                 ref={props.ref}
                />
                <DialogActions>                
                
                <Button 
                
                onClick={props.handleClose}>
                    Annuler
                </Button>
                <Button color="primary"
                onClick={props.handleSubmit}>
                   Ajouter
                </Button>
                
            </DialogActions>
            </DialogContent>
           
        </Dialog>
    )
}

export default FormDialogComponent
