import React from 'react'
import renderHTML from 'react-render-html';
import Moment from 'react-moment';


import BackLink from '../../Back/BackLink';
import Marker from '../../Marker';
import NavBg from '../../Menu/Toolbar/NavBg';
import ShareBtn from 'components/Share/ShareBtn';
import HalfRating from 'components/Rating';
import { Link } from 'react-router-dom';
import Rater from 'components/Rater';

const ContentSingleConseil = ({conseil, background, src, handleClickShare}) => {
    return (
        <article key={ conseil.id }>
            <nav>
                <BackLink/>
                <NavBg><h1>{conseil.title.rendered}</h1></NavBg>   
            </nav>  
            
            <div className="post-image" style={background}>
                
                <img className="center" src={src} />
                
                
            </div> 
            <div className="post-content">
                <Marker />
                
                {/*<div className="post-meta clearfix" >
                    <div className="post-date" ></div>
                    
                    <div className="cat"><a href="">{categories}</a></div>
                    
                    
                </div>*/}
            
                <div className="post-title"> <h1>{conseil.title.rendered}</h1> </div>                                
                <div className="content" >{ renderHTML ( conseil.content.rendered )}</div>
            </div> 

            <div className="metaData">
                <div className="date">
                    <p className="publi-jour">
                        <Moment format="DD">{conseil.date}</Moment> 
                    </p>
                    <p className="publi-mois"> 
                        <Moment format="MMM" locale="fr">{conseil.date}</Moment>
                    </p>
                </div>
            </div> 

            <div className="post-footer">
                <div className="share">
                    <span  style={ styles.text}>Partagez cette article</span>  
                    <ShareBtn  style={ styles.sharebtn} handleClick={handleClickShare} />
                </div>
                
                 <div className="rate" >
                    Les avis sur cette article : <Link></Link> Avis <HalfRating /> 
                    
                </div>  
            </div>             
        </article>
    )
}
const styles = {    
    sharebtn: {
        marginBottom: '10px',
        color: 'rgb(199 78 118)',
    },
    text: {
        fontWeight: 'bold',
        color: 'rgb(56 56 56)',
    }
}
export default ContentSingleConseil
