import React, { useState, useContext}  from 'react';
import ImagePP from '../../../images/20191116_171951.jpg';

import AppContext from "../../context/AppContext";
import { SideDrawerDataTop, SideDrawerDataMiddle, SideDrawerDataBottom } from './SideDrawerData';
import { BsPencil } from "react-icons/bs";

import { ReactComponent as Logout }   from '../../../images/icons/logout.svg';
import { ReactComponent as Login }   from '../../../images/icons/login.svg';

import './SideDrawer.css';
import { FaSignInAlt, FaSignOutAlt, FaShareAlt } from 'react-icons/fa';
import { isLoggedIn } from "../../functions";
import { useHistory } from "react-router-dom";



const SideDrawer = props => {
    let drawerClasses = 'side-drawer';
    if (props.show){
        drawerClasses = 'side-drawer open';
    }
    const [ store, setStore ] = useContext( AppContext );
    const handleLogout = () => {
        localStorage.removeItem( 'token' );
        localStorage.removeItem( 'useName' );
    
        setStore( {
            ...store,
            token: '',
            useName: ''
        } );
        window.location.href = '/';
    };
    const history = useHistory();
    const handleClickSign = () => {
        history.push('/Sign');
      }

    return (
       
      <nav className={drawerClasses}>
        <div id="mySidebar" className="sidebar">
            <div className="sidebarBg">
                <div className="pp-container">
                    <img src={ImagePP} alt=""/>
                    <button onClick={() => {window.location.pathname = '/Profile'}}><BsPencil/> Modifier le profil</button>
                </div>

                <div className="sidenav-top nav-items-group">
                    {SideDrawerDataTop.map((val, key )=>{
                        return (
                            <button 
                            key={key} 
                            className={`menu-item  ${val.classname}`} 
                            id={window.location.pathname == val.link ? "currentPage" : ""}
                            onClick={() => {window.location.pathname = val.link}} 
                            >
                                <div className="menu-item-title">
                                    <span className={val.iconClass} >
                                        {val.icon}
                                    </span>
                                    {val.title}
                                </div>
                                
                            </button>
                        )
                    })}
                    
                </div>

                <div className="sidenav-middle nav-items-group">
                    {SideDrawerDataMiddle.map((val, key )=>{
                        return (
                            <button 
                                key={key} className={`menu-item  ${val.classname}`} 
                                id={window.location.pathname == val.link ? "currentPage" : ""}
                                onClick={() => {window.location.pathname = val.link}}>
                                <div className="menu-item-title">
                                    <span className={val.iconClass} >{val.icon}</span>{val.title} 
                                </div>
                                
                            </button>
                        )
                    })}
                </div>   
                    
                <div className="sidenav-bottom nav-items-group">
                    {SideDrawerDataBottom.map((val, key )=>{
                        return (
                            <button 
                                key={key} className={`menu-item  ${val.classname}`} 
                                id={window.location.pathname == val.link ? "currentPage" : ""}
                                onClick={() => {window.location.pathname = val.link}}>
                                <div className="menu-item-title">
                                    <span className={val.iconClass} >{val.icon}</span>{val.title} 
                                </div>
                                
                            </button>
                        )
                    })}

                    <button 
                            className="menu-item share-sidebar" 
                            onClick={props.handleClickShare}>
                        <div className="menu-item-title">
                            <span className="icon-share" ><FaShareAlt/></span>Partager 
                        </div>
                        
                    </button>

                    { isLoggedIn() ? 
                    (
                        <button 
                            className="menu-item lgout-sidebar " 
                            onClick={ handleLogout }>
                                <div className="menu-item-title">
                                    <span className="icon-lgout" ><Logout/></span>Deconnexion
                                </div>
                                
                        </button>
                    ) : (                        
                            
                        <button onClick={ handleClickSign } className="menu-item lgin-sidebar "  >
                            <div className="menu-item-title">
                                <span className="icon-lgin" ><Login/></span>Se connecter
                            </div>                             
                        </button>
                           
                                          
                    ) }
                            

                            

                </div>
                                
            </div>          
        </div>
      </nav>
    );

  
}

export default SideDrawer ;
