import React, {Component} from 'react';
import axios from 'axios';
import renderHTML from 'react-render-html';
import Moment from 'react-moment';
import clientConfig from "../../../client-config";
import ShareLinks from '../../Share/ShareLinks';
  
import  './SingleRecette.css';
import BackLink from '../../Back/BackLink';
import Marker from '../../Marker';
import NavBg from '../../Menu/Toolbar/NavBg';
import Loader from '../../../images/Double Ring-1s-200px.gif'
import HalfRating from 'components/Rating';
import ContentSingleRecette from './ContentSingleRecette';
import BackgroundShare from 'components/Share/BackgroundShare';


class SingleRecette extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            post: {},
            error: '',
            showShare: false
        }
    }
       
    createMarkup = ( data ) => ({
        __html: data
    });

    componentDidMount() {
        
        const siteUrl = clientConfig.siteUrl;
        this.setState({loading:true}, ()=>{
            axios.get(`${siteUrl}/wp-json/wp/v2/recette/${this.props.match.params.id}?_embed`)
            .then( res => {
                this.setState({loading:false, post: res.data})
            })
            .catch( error => this.setState({loading:false, error: error.response.data.message}))
        })
        
    }

    handleShare = () => {
        this.setState((prevState) => {
          return {showShare: !prevState.showShare};
        });
    };
    backdropClickHandler = () => {
        this.setState({showShare: false});
      }
    
    render() {
        const { post, loading, error } = this.state;
        const wPsiteUrl = clientConfig.siteUrl;
        let backdrop;
            if (this.state.showShare){
            backdrop =<BackgroundShare click={this.backdropClickHandler} />;
        }
        return (
                            
            <div className="single-page-recettes page" style={{minHeight:'100%'}}>
            
                <div className="single-recette-wrapper">    
                   

                    <div className="rct-art-bg" ></div>
                    
                    <div className="wrap-article single-recette" style={styles.page}> 
                        {error && <div className="alert-error">{error}</div>}
                        { Object.keys( post).length ?  (
                            <>
                                <ContentSingleRecette 
                                post={post} src={post._embedded['wp:featuredmedia'][0].source_url} 
                                background={{background: `url(${post._embedded['wp:featuredmedia'][0].source_url})`}} 
                                alt={post._embedded['wp:featuredmedia'][0].source_url.slug} 
                                handleClickShare={this.handleShare}
                                />
                                <ShareLinks title="cette article"  size={36} show={this.state.showShare} url={`${wPsiteUrl}/recette/${post.id}`} />
                            </>
                         ) : ''}
                        {loading && <img className="loader" src={Loader} alt="loader"/>}
                        </div>
                       
                </div>
                {backdrop}
               
            </div>


        
    
            
        )
    }
}

const styles = {
    card: {
        objectFit: 'cover',
        width:' 100%',
        height: 'inherit',
        position: 'relative'
       
    }, 
    page: {
        position: 'relative', 
        minHeight : '100vh'
    }
    
}

export default SingleRecette;