import React, {Component } from 'react';
import axios from 'axios';
import clientConfig from "../../client-config";
import ContentRecette from './ContentRecette';
import Loader from '../layouts/Loader';

import {Swiper, SwiperSlide} from 'swiper/react';
import SwiperCore, {Navigation, Pagination, Controller, Thumbs} from 'swiper';
import 'swiper/swiper-bundle.css';

import ShareLinks from 'components/Share/ShareLinks';
import Backdrop from 'components/Menu/Backdrop';


SwiperCore.use([Navigation, Pagination, Controller, Thumbs]);

class RecetteList extends Component {
    constructor( props ) {
         super( props );
 
         this.state = {
             loading : false,
             posts: [],
             error: ''
         };
     }
 
     createMarkup = ( data ) => ({
         __html: data
     });
  
     componentDidMount() {
         const siteUrl = clientConfig.siteUrl;
         this.setState( { loading: true }, () => {
             axios.get(`${siteUrl}/wp-json/wp/v2/recette?_embed&per_page=100&filter[orderby]=date&order=asc`)
             .then( res => {
                 if ( 200 === res.status ) {
                     if ( res.data.length ) {
                         this.setState( { loading: false, posts: res.data } );
                     } else {
                         this.setState( { loading: false, error: 'Aucun article trouvé' } );
                     }
                 }
 
             } )
             .catch( err => this.setState( { loading: false, error: err } ) );
         } )       
     }
     
     
     render() {
     
         const {posts, loading, error} = this.state;
         const {genre, show, showShare} = this.props
 
         
         return (
             <>
             { error && <div className="alert-error" dangerouslySetInnerHTML={ this.createMarkup( error ) }/> }
                     {posts.length > 0 ?  (                             
             <Swiper 
                 breakpoints={{
                 // when window width is >= 360px
                 360: {
                 width: 360,
                 slidesPerView: 3,
                 },
                 // when window width is >= 640px
                 640: {
                     width: 640,
                     slidesPerView: 4,
                 },
                 // when window width is >= 768px
                 768: {
                     width: 768,
                     slidesPerView: 5,
                 },
                 }}
                 id="main"
                 
                 tag="section" 
                 wrapperTag="ul"  
                 navigation 
                 spaceBetween={0} 
                 slidesPerView={3}
                 >
                         
                     {posts.filter(post => post.genre[0].slug === `${genre}`).map((filteredPost, index) => {
                         return(
                             <React.Fragment>
                                 <SwiperSlide 
                                 key={index} 
                                 style={{listStyle:'none'}}
                                 tag="li" >
                                     
                                     <ContentRecette  
                                     background={filteredPost._embedded['wp:featuredmedia'][0].source_url}  
                                     link={`/recette/${filteredPost.id}`}
                                     handleClickShare={this.handleShare}                                    
                                     >
                                         {filteredPost.title.rendered}
                                     </ContentRecette>
                                 </SwiperSlide>
                                 <ShareLinks 
                                 title="cette recette avec vos amis :"  
                                 show={show} 
                                 url={`/recette/${filteredPost.id}`} 
                                 value={`/recette/${filteredPost.id}`}
                                 showShare={showShare}/>
                             </React.Fragment>                             
                         )
                     })}                   
                   
             </Swiper>
             ) : ''}{loading && <Loader />}
             </>
         )
     }
 
 }

export default RecetteList