import React, {useState} from 'react'
import {Button, DialogContent, Dialog, DialogTitle,  DialogActions } from '@material-ui/core'

const CreateTask = (props) => {
    const [taskName, setTaskName] = useState('')
    const [description, setDescription] = useState('')

    const handleChange = (e) => {
        const {name, value} = e.target
        if (name === "taskName" ){
            setTaskName(value)
        }else {
            setDescription(value)
        }
    }
    const handleSave = (e) => {
        let taskObj = {}        
        taskObj['Name'] = taskName
        taskObj['Description'] = description
        props.save(taskObj)
        setTaskName('')
        setDescription('')
    }
    return (
        <div>
            <Dialog open={props.open}  style={{minWidth: "60vw"}}> 
            <DialogTitle>
                Add Todo
            </DialogTitle>
            <DialogContent>
                <form >
                <div className="form-group">
                    <input 
                    type="text" 
                    className='form-control' 
                    name="taskName" value={taskName} 
                    onChange={handleChange}
                    placeholder="étape"/>
                </div>
                <br/>
                <br/>
                <div className="form-group">
                    <input 
                    type="text" 
                    className='form-control' 
                    name='description' 
                    value={description} 
                    onChange={handleChange}
                    placeholder="produit"/>
                </div>
                </form>
            </DialogContent>
            <DialogActions>
                
                
                <Button 
                
                onClick={props.handleClose}>
                    Annuler
                </Button>
                <Button color="primary"
                onClick={handleSave}>
                    Ajouter
                </Button>
                
            </DialogActions>
           
        </Dialog>
            
        </div>
    )
}

export default CreateTask
