import React, {Component} from 'react';
import axios from 'axios';
import renderHTML from 'react-render-html';
import Moment from 'react-moment';


import BackLink from '../Back/BackLink';
import Marker from '../Marker';
import NavBg from '../Menu/Toolbar/NavBg';
import Loader from '../../images/Double Ring-1s-200px.gif'


export default class SingleCoiffure extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            post: {},
            error: ''
        }
    }
       
    

    componentDidMount() {
        
        const wordPressSiteUrl = "http://localhost/wordpress/";
        this.setState({loading:true}, ()=>{
            axios.get(`${wordPressSiteUrl}/wp-json/wp/v2/v2/coiffures/${this.props.match.params.id}?_embed`)
            .then( res => {
                this.setState({loading:false, post: res.data})
            })
            .catch( error => this.setState({loading:false, error: error.response.data.message}))
        })
        
    }
    
    render() {
       
        const { post, loading, error } = this.state;
        
        return (
            <>
            {error && <div className="alert-error">{error}</div>}
            { Object.keys( post).length ?  (
            <div className="single-page-conseil  page">
                     
            <div className="rct-art-bg"></div>
            
            <div className="wrap-article single-conseils"> 
                <article key={ post.id }>
                    <nav>
                        <BackLink/>
                        <NavBg><h1>{post.title.rendered}</h1></NavBg>   
                    </nav>  
                    
                    <div className="post-image" style={{background: `url(${post._embedded['wp:featuredmedia'][0].source_url})`}}>
                        
                        <img className="center" src={post._embedded['wp:featuredmedia'][0].source_url} alt={post._embedded['wp:featuredmedia'][0].slug} />
                        
                        
                    </div> 
                    <div className="post-content">
                        <Marker/>
                        {/*<div className="post-meta clearfix" >
                            <div className="post-date" ></div>
                            
                             <div className="cat"><a href="">{categories}</a></div>
                             
                            
                        </div>*/}
                       
                        <div className="post-title"> <h1>{post.title.rendered}</h1> </div>                                
                        { renderHTML ( post.content.rendered )}
                    </div> 

                    <div className="metaData">
                            <div className="date">
                                <p className="publi-jour">
                                    <Moment format="DD">{post.date}</Moment> 
                                </p>
                                <p className="publi-mois"> 
                                    <Moment format="MMM" locale="fr">{post.date}</Moment>
                                </p>
                            </div>
                        </div> 
                    <div className="post-footer">
                           
                    </div>             
                </article>
            </div>
                             
        
      
        
        </div>
         ) : ''}
         {loading && <img className="loader" src={Loader} alt="loader"/>}
        </>
        )
    }
}


