import React, {Component } from 'react';
import axios from 'axios';

import {Link} from "react-router-dom";
import Loader from '../../layouts/Loader';


import clientConfig from "../../../client-config";


class BoxConseils extends Component {
    constructor( props ) {
		super( props );

		this.state = {
			loading : false,
			conseils: [],
			error: ''
		};
	}

	createMarkup = ( data ) => ({
		__html: data
	});

    componentDidMount() {
        const wordPressSiteUrl = clientConfig.siteUrl;
        this.setState( { loading: true }, () => {
            axios.get(`${wordPressSiteUrl}/wp-json/wp/v2/conseil?_embed&per_page=4`)
            .then( res => {
                if ( 200 === res.status ) {
                    if ( res.data.length ) {
                        this.setState( { loading: false, posts: res.data } );
                    } else {
                        this.setState( { loading: false, error: 'Aucun article trouvé' } );
                    }
                }

            } )
            .catch( err => this.setState( { loading: false, error: err } ) );
        } )
        
    }
    render() {
        const {conseils, loading, error} = this.state;
        const SiteUrl = "http://localhost/wordpress/";
        return (        
        
            <div className="conseils-content panel-content clearfix">
                { error && <div className="alert-error" dangerouslySetInnerHTML={ this.createMarkup( error ) }/> }
                 {conseils.length > 0 ?  (
                    conseils.map((conseil, index) =>{
                    return(
                        
                        <article key={index} className="post-item" >
                            <div className="post-wrap"  style={{  
                                position: 'relative',
                                height: '100%', 
                                background: `url(${conseil._embedded['wp:featuredmedia'][0].source_url})`, 
                                backgroundSize: 'cover',
                                backgroundRepeat:'no-repeat',
                                backgroundPosition: 'center',}}>
                            
                                <div className="post-entry" >
                                    
                                    <Link to={`/conseil/${conseil.id}`}>
                                        <h2>{conseil.title.rendered}</h2>
                                    </Link>                                                  
                                   
                                </div>
                            </div>
                        </article>
                    )
                })
                ) : ''}
                {loading && <Loader />}
            </div>
        )
    }
}

const styles = {
    image: {

        backgroundSize: 'cover !important',
        backgroundRepeat:' no-repeat !important',
        backgroundPosition: 'center !important',
        position: 'relative',
        height: '100%',
        },
    sharebtn: {
        marginBottom: '10px',
        color: 'rgb(199 78 118)',
    },
    text: {
        fontWeight: 'bold',
        color: 'rgb(56 56 56)',
    }
}


export default BoxConseils;