import React, {useState, useRef} from 'react'
import { FacebookIcon, FacebookShareButton, TwitterIcon, TwitterShareButton, WhatsappIcon, WhatsappShareButton } from 'react-share'
import "./Share.css";

const ShareLinks = ({url, show, title, value}) => {
    let shareClasses = 'share-btn';
    if (show){
        shareClasses = 'share-btn open';
    }

    const [copySuccess, setCopySuccess] = useState(false);
    const textAreaRef = useRef(null);

    function copyToClipboard(e) {
        textAreaRef.current.select();
        document.execCommand('copy');
        e.target.focus();
        setCopySuccess(true);
    };

   
    return (
        
        
        <div className={shareClasses} >
           <h2>Partagez {title} à vos amis </h2>
            <div className="share-content" style={{display: 'flex', flexDirection: 'column'}}>
            
                <FacebookShareButton url={url} hashtag="#lovinmyhair ">
                    <FacebookIcon  size={36} logoFillColor="white" round={true} ></FacebookIcon>
                </FacebookShareButton>

                <WhatsappShareButton url={url} hashtag="#lovinmyhair #hairapp">
                    <WhatsappIcon  size={36} logoFillColor="white" round={true} ></WhatsappIcon>
                </WhatsappShareButton>
                
                <TwitterShareButton url={url} hashtag="#lovinmyhair #hairapp">
                    <TwitterIcon size={36} logoFillColor="white" round={true} >
                    </TwitterIcon>
                </TwitterShareButton>
            </div>
            <div className="copyLink">
                {       
                    document.queryCommandSupported('copy') &&
                    <>
                        <button 
                        
                        className={copySuccess ? "copyLinkBtn active" : "copyLinkBtn" }
                        onClick={copyToClipboard}>{copySuccess ? "Copié" : "Copier"}</button> 
                        
                    </>
                }
                <input
                className={copySuccess ? "shareLink active" : "shareLink" }
                ref={textAreaRef}
                value={value}
                />
            </div>
            
        
      
        </div>
        
    )
}

export default ShareLinks
