import React, { useState, useContext} from 'react';
import DrawerToggleBtn from './DrawerToggleBtn';
import './Toolbar.css'
import ShopBtn from '../../Shop/ShopBtn';
import { FaSearch, FaSignInAlt, FaSignOutAlt} from "react-icons/fa";
import { useHistory } from "react-router-dom";
import { isLoggedIn } from "../../functions";
import { Badge, Tooltip } from '@material-ui/core';
import NotificationsIcon from '@material-ui/icons/Notifications';
import MyNavlink from './MyNavlink'

const Toolbar = (props) => {
    let toggleClasses = 'toolbar';
    if (props.closeBtn){
        toggleClasses = 'toolbar toolbar-active';
    }
    const [toolbar,setToolbar] = useState(false);
    
    const changeBackground = () => {
       
        if (window.scrollY >=80) {
            setToolbar(true);

        } else {
            setToolbar(false);
        }
    };
    window.addEventListener('scroll', changeBackground);

    const ToolbarNavLinks = [
        {
            children: <ShopBtn/>,
            link: "Shop",        
            classname: "shoplink",
        },
        {
            children: <FaSearch/>,
            link: "/Search",
            classname: "searchBtn"
        },        
    ] 

    const history = useHistory();
    const handleClick = () => {
        history.push('/');
      }
    return (
        <div className={toggleClasses}>
            <div className={toolbar ? 'nav-bg nav-bg-active': 'nav-bg'} ></div> 
            <nav className='toolbar-nav '> 
                <div className="toolbar-logo" href="/">Lovin<strong>My</strong><span>Hair</span> 
                </div>

                <div className="spacer"></div>
                {ToolbarNavLinks.map((nav, index) => (
                    <Tooltip title="Shop"> 
                        <MyNavlink  className={nav.classname} link={nav.link}>
                            {nav.children}
                        </MyNavlink>
                    </Tooltip> 

                ))}
                
                <Tooltip title="Menu">
                    <div className='toggleBtn' >                    
                            <DrawerToggleBtn click={props.drawerToggleClickHandler} />
                    </div>
                </Tooltip> 
            </nav>
        </div>
      
    );

  
}

export default Toolbar;
