import React, {useState} from 'react';

import Header from '../Header';
import Main from '../Wrapper/Main';

import './Home.css';
import {   useParams
} from "react-router-dom";

import Swiper from './Myswiper';
import Accordion from './Accordion'
import { tabsDatas } from './TabsData'
import { getUserName } from "../functions";
import { Link } from 'react-router-dom';
import { FaLongArrowAltRight } from "react-icons/fa";
import  { ReactComponent as RightGrey }  from '../../images/icons/right-grey.svg'


const Home = () =>  {

  const [clicked, setClicked] = useState(false);
  
  const toggle = index =>{
    if (clicked === index){
      return setClicked(null)
    }
    setClicked(index)
  }
  const userName = ( getUserName() ) ? getUserName() : '';

  
    return (
      
      <div className="page Home">
        <Header title="Bonjour," username={userName}>
            In nimia in agitationibus summum species sudant solito longiores.
        </Header>
       
        <Main >
        <div className="home">
            <div className="news-container">
              <div className="news-bg"></div>
                      
              <div className="news-wrap">
                <h1>
                  <div className="news-titre" >
                    Styles de coiffures
                  </div>
                  
                   
                    <div className="news-link" >
                      <Link to='/Coiffures' >
                        
                        <RightGrey />
                        </Link>
                    </div>
                  
                </h1>
                
                <div className=" news-content"  id="news-carousel">
                  <Swiper/>
                  
                </div>
                  
              </div>
            </div>

            <div className="tabcontainer">
              {tabsDatas.map((item, index) =>{ 
                return(
                  <Accordion 
                  key={index} title={item.title} className={item.className} 
                  onClick={()=> toggle(index)} link={item.link}
                   more={item.more} small={item.small} clicked={clicked} index={index} >
                   {item.children}
                  </Accordion>
                )
              })}
                  

            </div>
          </div>
        </Main>
          
      </div>
       
    );
 
  
}

export default Home;
