import React, {Component} from 'react';
import axios from 'axios';
import renderHTML from 'react-render-html';
import Moment from 'react-moment';


import BackLink from '../../Back/BackLink';
import Marker from '../../Marker';
import NavBg from '../../Menu/Toolbar/NavBg';
import Loader from '../../../images/Double Ring-1s-200px.gif'

import './SingleProduits.css';


class SingleContentProduct extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: false,
            produit: {},
            error: ''
        }
    }
       
    

    componentDidMount() {
		const wordPressSiteURL = "http://localhost/wordpress/";

		this.setState( { loading: true }, () => {
			axios.get( `${wordPressSiteURL}/wp-json/wp/v2/produit/${this.props.match.params.id}?_embed` )
				.then( res => {
					console.warn( res.data );
					if ( Object.keys( res.data ).length ) {
						this.setState( { loading: false, produit: res.data } );
					} else {
						this.setState( { loading: false, error: 'Aucun article trouvé' } );
					}
				} )
				.catch( err => this.setState( { loading: false, error: err.response.data.message } ) );
		} )
	}
    
    render() {
       
        const { produit, loading, error } = this.state;
        console.warn( produit );
        return (
        
            <div className="single-page-products page" style={styles.page}>
                
                    <div className={`single-produit-wrapper ${produit.slug}`} style={{position: 'relative', height : '100%'}} >    
                        <div className="single-produit-content" style={{position: 'relative', height : '100%'}}>                    
                            <div className="wrap-article single-produits" style={{position: 'relative', height : '100%'}}> 
                                {error && <div className="alert-error">{error}</div>}
                                { Object.keys( produit).length ?  (
                                    <article>  
                                        <nav>
                                            <BackLink/>
                                            <NavBg><h1>{produit.title.rendered}</h1></NavBg>   
                                        </nav>     
                                                

                                        <div className="post-image" style={{background: `url(${produit._embedded['wp:featuredmedia'][0].source_url})`}}>
                                            <img className="center" src={produit._embedded['wp:featuredmedia'][0].source_url} style={styles.card} alt={produit._embedded['wp:featuredmedia'][0].slug} /> 
                                            <Marker/>                               
                                        </div>
                                        <div className="post-content">
                                            
                                            <div className="post-title"> <h1>{ produit.title.rendered }</h1> </div>                                
                                            <div className="content"> { renderHTML ( produit.content.rendered )} </div>
                                        </div>                   
                                        
                                    </article>
                                    ) : ''}
                                    {loading && <img className="loader" src={Loader} alt="loader"/>}   
                            </div>
                        </div>         
                    </div> 
        
            </div>           
        )
    }
}
const styles = {
    card: {
        objectFit: 'cover',
        width:' 100%',
        height: 'inherit',
        position: 'relative'
       
    }, 
    page: {
        position: 'relative', 
        height : '100vh'
    }
    
}
export default SingleContentProduct;
