import React from 'react';
import ShareBtn from 'components/Share/ShareBtn';

import {
  Link,

} from "react-router-dom";
import Marker from '../Marker';

const ContentRecette = ({children, background, link, alt, genre, className, handleClickShare}) => {
    return (
            <article className="recette-box">
                
                <div className={`${className}  recette-thumbnail `} >
                    <img src={background} alt={alt}/>
                    <div className="post-cover"></div>
                </div>
                 


                <div className="post-title">
                    <h2 className="recette-title"> 
                        <Link  to={link}>{ children }</Link> 
                        <ShareBtn  style={ styles.sharebtn} handleClick={handleClickShare}  />
                    </h2>

                    <h3>{genre}</h3>
                        
                    <Marker/>
                </div>
                
            </article>
 
           
    )
}
const styles = {
    card: {
        objectFit: 'cover',
        width:' 100%',
        height: 'inherit',
        position: 'relative',
        borderRadius: '5% 95% 5% 95% / 77% 21% 79% 23%'
    },
    sharebtn: {
        color: '#fff'
    }
}

export default ContentRecette;