import React, {Component } from 'react';
import axios from 'axios';

import Loader from '../../layouts/Loader';
import {Swiper, SwiperSlide} from 'swiper/react';
import SwiperCore, {Navigation, Pagination, Controller, Thumbs} from 'swiper';
import 'swiper/swiper-bundle.css';
import ProduitsBoxes from '../../Produits/ProduitsBoxes';
import clientConfig from "../../../client-config";

SwiperCore.use([Navigation, Pagination, Controller, Thumbs]);



class BoxProduits extends Component {
  constructor( props ) {
		super( props );

		this.state = {
			loading : false,
			posts: [],
			error: ''
		};
	}

	createMarkup = ( data ) => ({
		__html: data
	});

  componentDidMount() {
    const wordPressSiteUrl = clientConfig.siteUrl;
      this.setState({loading:true}, ()=>{
        axios.get(`${wordPressSiteUrl}/wp-json/wp/v2/produit?_embed&per_page=6`)
        .then( res => {
          if ( 200 === res.status ) {
              if ( res.data.length ) {
                  this.setState( { loading: false, posts: res.data } );
              } else {
                  this.setState( { loading: false, error: 'Aucun article trouvé' } );
              }
          }

      } )
      .catch( err => this.setState( { loading: false, error: err } ) );
    } )
      
  }

  render() {
    const {posts, loading, error} = this.state;
   
    { /*const [thumbsSwiper, setThumbsSwiper] = useState(null);*/}
  

    return (
        
      
      <div className="produits-content panel-content clearfix">
        { error && <div className="alert-error" dangerouslySetInnerHTML={ this.createMarkup( error ) }/> }
        {posts.length > 0 ?  (
        <Swiper 
          breakpoints={{
            // when window width is >= 480px
            480: {
              width: 480,
              slidesPerView: 3,
              },
              // when window width is >= 768px
              768: {
              width: 768,
              slidesPerView: 5,
              },
              // when window width is >= 1080px
              1070: {
                  width: 1070,
                  slidesPerView: 6,
              },
            }}
            id="thumbs"                        
            tag="section" 
            wrapperTag="ul"  
            navigation 
            spaceBetween={0} 
            slidesPerView={2}          
        >
          {posts.map((post, index) =>{
            return(
              <SwiperSlide 
              key={index} 
              style={{listStyle:'none'}}
              tag="li" >
                <ProduitsBoxes 
                key={index} 
                background={post._embedded['wp:featuredmedia'][0].source_url} 
                link={`/produit/${post.id}`} 
                title={post.title.rendered}>
                </ProduitsBoxes>
              </SwiperSlide>
            )
          })}
        </Swiper>
      ) : ''}
      {loading && <Loader />}
      </div>
               
    )
  }
}

export default BoxProduits
