import React, {Component} from 'react';
import axios from 'axios';


import {Link} from "react-router-dom";

import Loader from '../../layouts/Loader';

import {Swiper, SwiperSlide} from 'swiper/react';
import SwiperCore, {Navigation, Pagination, Controller, Thumbs} from 'swiper';
import 'swiper/swiper-bundle.css';
import HalfRating from 'components/Rating';
import ShareBtn from 'components/Share/ShareBtn';
import clientConfig from "../../../client-config";

SwiperCore.use([Navigation, Pagination, Controller, Thumbs]);

class BoxRecettes  extends Component {
    constructor( props ) {
		super( props );

		this.state = {
			loading : false,
			posts: [],
			error: ''
		};
	}

	createMarkup = ( data ) => ({
		__html: data
	});

    componentDidMount() {
        const wordPressSiteUrl = clientConfig.siteUrl;
        this.setState({loading:true}, ()=>{
            axios.get(`${wordPressSiteUrl}/wp-json/wp/v2/recette?_embed&per_page=6`)
            .then( res => {
                if ( 200 === res.status ) {
                    if ( res.data.length ) {
                        this.setState( { loading: false, posts: res.data } );
                    } else {
                        this.setState( { loading: false, error: 'Aucun article trouvé' } );
                    }
                }

            } )
            .catch( err => this.setState( { loading: false, error: err } ) );
        } )
        
    }
    render() {
        const {posts, loading, error} = this.state;
    
        const content = posts.map((post, index) =>{
            return(
                <SwiperSlide 
                key={index} virtualIndex={index}
                style={{listStyle:'none'}}
                tag="li" >
                    <article className="recette-box" key={index} >
                        
                        <div className="recette-image" style={{background: `url(${post._embedded['wp:featuredmedia'][0].source_url})`}}>
                            <img className="center" src={post._embedded['wp:featuredmedia'][0].source_url} alt="index"/>
                            <div className="post-cover"></div>
                            <div className="post-footer">
                                <div className="share"> 
                                    <ShareBtn  style={ styles.sharebtn} handleClick={this.handleClickShare} />
                                </div>
                                
                                <div className="rate" >
                                    <Link>Avis</Link>  <HalfRating /> 
                                </div>  
                            </div>  
                        </div>
                                                    
                        <div className="post-title">
                            <h2 className="recette-title"><Link to={`/recette/${post.id}`} >{post.title.rendered}</Link></h2>
                            <button className="plus-btn"></button>
                        </div>
                         
                    </article>
                </SwiperSlide>
            )
        } )

        return (
        
            <div className="recettes-content panel-content clearfix">
                { error && <div className="alert-error" dangerouslySetInnerHTML={ this.createMarkup( error ) }/> }
                {posts.length > 0 ?  (
                    <Swiper 

                        breakpoints={{                        
                            // when window width is >= 480px
                            480: {
                            width: 480,
                            slidesPerView: 3,
                            },
                            // when window width is >= 768px
                            768: {
                            width: 768,
                            slidesPerView: 5,
                            },
                            // when window width is >= 1080px
                            1070: {
                                width: 1070,
                                slidesPerView: 6,
                            },
                        }}
                        id="thumbs"                        
                        tag="section" 
                        wrapperTag="ul"  
                        navigation 
                        spaceBetween={0} 
                        slidesPerView={2}
                        
                    >
                        {content}
                    </Swiper>
                  ) : ''}
                  {loading && <Loader />}
            </div>
        )
    }
}
const styles = {
    sharebtn: {
        position: 'absolute',
        bottom: '20px',
        right: '10px',
        color: '#fff'
    },
    text: {
        fontWeight: 'bold',
        color: 'rgb(56 56 56)',
    }
}
export default BoxRecettes

