import React from 'react';

import  './Coiffures.css';


import ScrollToTop from '../../AppRoute/ScrollToTop';

import {     Switch, Route, withRouter
} from "react-router-dom";

import CoiffuresRoute from './CoiffuresRoute';
import SingleCoiffure from './SingleCoiffure';


const Coiffures = () => {
    return (
        <>
            <Route render={({location}) => (
                 
                <div className="coiffures page">
                
                <Switch location={location}>
                    <ScrollToTop>
                        <Route path="/Coiffures"  exact component={CoiffuresRoute} />                          
                        <Route path="/coiffure/:id"  component={SingleCoiffure} />
                    </ScrollToTop>
                </Switch>
                
                </div>
            ) }/>
        </>
    )
}

export default withRouter(Coiffures);
