import React, {Component } from 'react';
import axios from 'axios';


import Main from '../Wrapper/Main'
import Loader from '../../images/Double Ring-1s-200px.gif'
import ImageCard from './ImageCard';
import HeaderCoiffures from './HeaderCoiffures';

class CoiffuresRoute extends Component {
    constructor(props) {
         super(props);
         this.state = {
             loading: false,
             posts: [],
             spans:0,
             error: ''
         }
        
     }
 
    componentDidMount() {
        const wordPressSiteUrl = "http://localhost/wordpress/";
        this.setState({loading:true}, ()=>{
            axios.get(`${wordPressSiteUrl}/wp-json/wp/v2/coiffures?_embed`)
            .then( res => {
                this.setState({loading:false, posts: res.data})
            })
            .catch( error => this.setState({loading:false, error: error.response.data.message}))
        })
            
    }
       
    
    render() {
    
     const {posts, loading, error} = this.state;
     

        return (
            <div className="page coiffures">
                
                
                <HeaderCoiffures >
                   
                    
                </HeaderCoiffures>
                
                <Main>
                    <div className="page-coiffures">
                        {error && <div className="alert-error">{error}</div>}
                        
                        {posts.length > 0 ?  (
                            <div className="coiffures-content article-container">
                                <div className="image-list">
                                
                                    {posts.map((post, index )=>{
                                        return(
                                            <div key={index} className="coiffure-img">                                                
                                                <ImageCard 
                                                    key={post.id} 
                                                    link={`/coiffure/${post.id}`}
                                                    src={post._embedded['wp:featuredmedia'][0].source_url} 
                                                />
                                            </div>
                                        )
                                    })} 
                                </div>
                            </div> 
                         ) : ''}
                        {loading && <img className="loader" src={Loader} alt="loader"/>} 
                    </div>
                </Main>
               
             
              </div>
                

        )
    }

}
export default CoiffuresRoute;
