import React, { Component } from 'react';

import Recettes from './RecettesLink';
import SingleRecette from './SingleRecettes/SingleRecette';

import './Recettes.css';

import {  BrowserRouter as Router, Switch, Route, withRouter
} from "react-router-dom";
import ScrollToTop from '../../AppRoute/ScrollToTop';



class RecettesPages extends Component {
  render() {
    return (      
      <ScrollToTop>  
        <Route render={({location}) => (
          <div  className="recettes page" style={{minHeight:'100%'}}>                
            <Switch location={location}>                                                            
              <Route exact path="/Recettes"  component={Recettes} />
              <Route path="/recette/:id"  component={SingleRecette} />
            </Switch>
          </div>
        ) }/>
      </ScrollToTop>           
    );
  }
}
  
export default withRouter(RecettesPages);