import React, {Fragment, useState, Component } from 'react';
import axios from 'axios';

import Loader from '../../layouts/Loader';
import Card from './Card'
import './MySwiper.css'

import {Swiper, SwiperSlide} from 'swiper/react';

import { Link } from 'react-router-dom';
import SwiperCore, {Navigation, Pagination, Controller, Thumbs} from 'swiper';
import 'swiper/swiper-bundle.css';
import { FaLongArrowAltRight } from "react-icons/fa";
import clientConfig from "../../../client-config";


SwiperCore.use([Navigation, Pagination, Controller, Thumbs]);

class MySwiper extends Component {
    constructor( props ) {
		super( props );

		this.state = {
			loading : false,
			posts: [],
			error: ''
		};
	}

	createMarkup = ( data ) => ({
		__html: data
	});

    componentDidMount() {
        const wordPressSiteUrl = clientConfig.siteUrl;
        this.setState( { loading: true }, () => {
            axios.get(`${wordPressSiteUrl}/wp-json/wp/v2/coiffures?_embed&per_page=6`)
            .then( res => {
                if ( 200 === res.status ) {
                    if ( res.data.length ) {
                        this.setState( { loading: false, posts: res.data } );
                    } else {
                        this.setState( { loading: false, error: 'Aucun article trouvé' } );
                    }
                }

            } )
            .catch( err => this.setState( { loading: false, error: err } ) );
        } )
        
    }
    
    render() {
        const {posts, loading, error} = this.state;     

        return (
            <Fragment>
                <div className="view-port" style={styles.view_port}>
                    <div className="card-container" >
                        { error && <div className="alert-error" dangerouslySetInnerHTML={ this.createMarkup( error ) }/> }
				
                        {posts.length > 0 ?  (
                            <Fragment>
                            
                            <Swiper 
                            breakpoints={{
                                // when window width is >= 400px
                                400: {
                                width: 400,
                                
                            },                            
                                // when window width is >= 600px
                                600: {
                                width: 600,
                                
                                },
                                // when window width is >= 800px
                                800: {
                                width: 800,
                                
                                },
                                // when window width is >= 1080px
                                1080: {
                                    width: 1080,                                
                                },                                
                            }}
                            
                                id="main"
                                tag="section" 
                                wrapperTag="ul"  
                                pagination
                                navigation 
                                spaceBetween={0} 
                                slidesPerView={1}
                                onInit={(swiper) => console.log('Swiper initializing')}
                                onSlideChange={(swiper) => {
                                    console.log('Slide index changed to: ', swiper.activeIndex)
                                    
                                }}
                                onReachEnd={() => console.log('Swiper end reached')}
                                >

                                    {posts.map((post, index )=> {
                                        return(
                                        <SwiperSlide 
                                        key={index} 
                                        style={{listStyle:'none'}}
                                        tag="li" >
                                            
                                            <Card 
                                            background={post._embedded['wp:featuredmedia'][0].source_url}
                                            title={post.title.rendered}
                                            type={post.type}
                                            link_coiffures={`/coifure/${post.id}`}
                                            />

                                        </SwiperSlide>
                                    )} )}   
                                    
                            </Swiper>
                           
                          </Fragment>
                         ) : ''}
                         {loading && <Loader/> }
                    </div>
                </div>
        
            </Fragment>
        )   
    }
}

const styles = {
    view_port : {
        overflow :'hidden',
        margin: '0 auto'
    },
   
    card_container: {
        display: 'flex',
        flexDirection: 'fit-content'

    }
}
export default MySwiper;
