import React, {useState} from 'react'
import Input from './SignComponents/Input';
import { ReactComponent as NextIcon }   from '../../images/icons/right-grey.svg';
import { Link } from 'react-router-dom'
import axios from 'axios';
import clientConfig from '../../client-config';
import AppContext from "../context/AppContext";
import Loader from '../layouts/Loader'
class SignupFormNew extends React.Component {

	constructor(){
    super();
    this.state = {
      username: '',
      email: '',
      password: '',
      confirmPassword: '',
      postCreated: false,
			loading: false,
			message: '',
    };
   
  }

	createMarkup = ( data ) => ({
		__html: data
	});

	handleFormSubmit = ( event ) => {
		event.preventDefault();

		this.setState( { loading: true } );

		const formData = {
			username: this.state.username,
            email: this.state.email,
			password: this.state.password,
			status: 'add'
		};

		const wordPressSiteUrl = clientConfig.siteUrl;
		const authToken = localStorage.getItem( 'token' );

		// Post request to create a post
		axios.post( `${ wordPressSiteUrl }/wp-json/jwt-auth/v1/token`, formData, {
			headers: {
				'Content-Type': 'application/json',
				'Authorization': `Bearer ${ authToken }`
			}
		} )
			.then( res => {

				this.setState( {
					loading: false,
					postCreated: !! res.data.id,
					message: res.data.id ? 'New post created' : ''
				} )
			} )
			.catch( err => {{

				this.setState( { loading: false, message: err.response.data.message } )
			}} )
	};

  handleChange = ( event ) => {

		this.setState( { [ event.target.name ]: event.target.value } );

	};
    /*
    
    const [error, setError] = useState('');
    const errorMsg = error !== '' && <span>{error.message}</span>;
    */
    render(){
        const inputfields = [
            {id: 'username', value: 'username', type: 'text', placeholder: 'nom'},
            {id: 'email', value: 'email', type: 'email', placeholder: 'email'},
            {id: 'password', value: 'password', type: 'password', placeholder: 'Mot de passe'},
            {id: 'confirmPassword', value: 'confirmPassword', type: 'password', placeholder: 'Confirmer le mot de passe'},
        ];
          
        const { username, email, password, confirmPassword} = this.state;
        const btn = username === '' || email === '' || password === '' || password != confirmPassword
        ? <button type="button" disabled className="submit goNextStep">Suivant<NextIcon/></button>
        : <button type="button" className="submit goNextStep">Suivant<NextIcon/></button>
        const { loading, message, postCreated } = this.state;
        return (
            <div className="base-container"> 
            { message ? <div className={ `alert ${ postCreated ? 'alert-success': 'alert-danger' }` } dangerouslySetInnerHTML={ this.createMarkup( message ) }/> : ''}
                <div className="form-title"><h1>Créez un compte et commencez votre routine !</h1></div>
                <form className="sign-formgroup" onSubmit={this.handleFormSubmit} >
                    
                
                    <div className="sign-box">
                        {/*errorMsg*/}                   
                        {inputfields.map((inputfield, val) => (
                            
                            <Input
                                className={inputfield.id}
                                type={inputfield.type} 
                                placeholder={inputfield.placeholder}
                                id={inputfield.id}
                                name={inputfield.id}
                                value={val.value}
                                onChange={this.handleChange} 
                                key={val}
                                htmlFor={inputfield.id}
                                
                            />
                        ))}

                    </div>

                    <div className="sign-footer">           
                        <div className="submitButton">
                            {btn}
                        </div>
                    </div>
                    { loading && <img className="loader" src={ Loader } alt="Loader"/> }
                </form>
            </div>
        )
                }
}

export default SignupFormNew
