import React, {Component } from 'react';

import Produits from './ProduitsParent';

import './Produits.css';

import {    BrowserRouter as Router, Switch, Route, withRouter
} from "react-router-dom";
import ScrollToTop from '../../AppRoute/ScrollToTop';
import SingleContentProduct from './ProductsPages/SingleContentProduct';


class ProduitsPage extends Component {

  
    render() {
         
      
      return (
      

          <Route render={({location}) => (

           <div className="produits page">
               
                  <Switch location={location}>
                    <ScrollToTop>
                          <Route path="/Produits"  exact component={Produits} />                          
                          <Route path="/produit/:id"  component={SingleContentProduct} />
                      </ScrollToTop>
                  </Switch>
                
                  </div>
          ) }/>
        
      );
    }
  }
  
  export default withRouter(ProduitsPage);




