import React, {useContext} from 'react';
import AppContext from "../context/AppContext";

import Main from '../Wrapper/Main'
import UserPageBtn from './UserPageBtn'
import ProfileImg from '../../images/pics/blank-profile-picture.png';

import { ReactComponent as LogOut }   from '../../images/icons/logout.svg';

import {    useParams, Link
} from "react-router-dom";

import './User.css';
import UserBottom from './UserBottom';
import { isLoggedIn } from "../functions";



const UserParent = (props) => {
   
    const {firstname} = useParams();

    const [ store, setStore ] = useContext( AppContext );

	const handleLogout = () => {
		localStorage.removeItem( 'token' );
		localStorage.removeItem( 'useName' );

		setStore( {
			...store,
			token: '',
			useName: ''
		} );
		window.location.href = '/User';
	};
    
    return (
        <React.Fragment>
            { isLoggedIn() ? (
            
                <div className="user-page-container">

                    <header className="app-header">
                        
                        <div className="header-bg">                
                            <div className="header-content">                
                                <h1>Mes réglages  
                                    
                                </h1>
                                <div className="user-profile">
                                    <strong className="username" >  
                                        {firstname}                      
                                    </strong>   
                                    <img src={ProfileImg} alt=""/>
                                </div>
                                <button className="logout-btn" onClick={handleLogout}><span className="icon-lgout"><LogOut/></span><span>Deconnexion</span></button>            
                            </div>
                        </div>
                    </header>  
                    
                    <Main >
                        <div className='user-page'>
                        
                            <UserPageBtn/>                        
                        <div className="carousel-box">
                    
                            </div>
                        <UserBottom/>
                        </div>
                    
                    </Main>   
                    <div className="sidebar">
                        {props.children}
                    </div>
                </div>
                ) : (
                <div className="connectionlink">
                    <div>
                       Accedez gratuitement à ce contenu en vous connectant.<br/>
                        <Link to="/Sign"> Connectez vous ici ! </Link>
                    </div>
                </div>
            )}
        </React.Fragment>
    )
  
}

export default UserParent;